export class JobItemViewController extends BaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {
			el: '#job_item_view_container',

			status_array: null,

			job_item_group_array: null,
			job_item_group_api: null,

			sub_document_view_controller: null,

			document_object_type_id: null,

			date_api: null
		} );

		super( options );
	}

	init( options ) {
		//this._super('initialize', options );
		this.edit_view_tpl = 'JobItemEditView.html';
		this.permission_id = 'job_item';
		this.viewId = 'JobItem';
		this.script_name = 'JobItemView';
		this.table_name_key = 'job_item';
		this.context_menu_name = $.i18n._( 'Tasks' );
		this.navigation_label = $.i18n._( 'Tasks' );

		this.document_object_type_id = 70;
		this.api = TTAPI.APIJobItem;

		this.date_api = TTAPI.APITTDate;

		this.job_item_group_api = TTAPI.APIJobItemGroup;

		this.render();
		this.buildContextMenu();

		this.initData();
	}

	initOptions() {
		var $this = this;

		this.initDropDownOption( 'status' );
		this.job_item_group_api.getJobItemGroup( '', false, false, {
			onResult: function( res ) {
				res = res.getResult();
				res = Global.buildTreeRecord( res );

				if ( !$this.sub_view_mode && $this.basic_search_field_ui_dic['group_id'] ) {
					$this.basic_search_field_ui_dic['group_id'].setSourceData( res );
					$this.adv_search_field_ui_dic['group_id'].setSourceData( res );
				}

				$this.job_item_group_array = res;

			}
		} );
	}

	getCustomContextMenuModel() {
		var context_menu_model = {
			exclude: [],
			include: [{
				label: $.i18n._( 'Import' ),
				id: 'import_icon',
				menu_align: 'right',
				action_group: 'import_export',
				group: 'other',
				vue_icon: 'tticon tticon-file_download_black_24dp',
				permission_result: PermissionManager.checkTopLevelPermission( 'ImportCSVJobItem' ),
				permission: null,
				sort_order: 9010
			}]
		};

		return context_menu_model;
	}

	setDefaultMenuImportIcon( context_btn, grid_selected_length, pId ) {
		if ( PermissionManager.checkTopLevelPermission( 'ImportCSVJobItem' ) === true ) {
			ContextMenuManager.hideMenuItem( this.determineContextMenuMountAttributes().id, context_btn.id, true );
		} else {
			ContextMenuManager.hideMenuItem( this.determineContextMenuMountAttributes().id, context_btn.id, false );
		}
	}

	onCustomContextClick( id ) {
		switch ( id ) {
			case 'import_icon':
				this.onImportClick();
				break;

		}
	}

	onImportClick() {
		var $this = this;
		IndexViewController.openWizard( 'ImportCSVWizard', 'JobItem', function() {
			$this.search();
		} );
	}

	buildEditViewUI() {

		super.buildEditViewUI();

		var $this = this;

		var tab_model = {
			'tab_task': { 'label': $.i18n._( 'Task' ), 'is_multi_column': true },
			'tab_attachment': true,
			'tab_audit': true,
		};
		this.setTabModel( tab_model );

		var form_item_input;
		var widgetContainer;

		this.navigation.AComboBox( {
			api_class: TTAPI.APIJobItem,
			id: this.script_name + '_navigation',
			allow_multiple_selection: false,
			layout_name: 'global_job_item',
			navigation_mode: true,
			show_search_inputs: true
		} );

		this.setNavigation();

		//Tab 0 start

		var tab_task = this.edit_view_tab.find( '#tab_task' );

		var tab_task_column1 = tab_task.find( '.first-column' );

		this.edit_view_tabs[0] = [];

		this.edit_view_tabs[0].push( tab_task_column1 );

		//Status

		form_item_input = Global.loadWidgetByName( FormItemType.COMBO_BOX );
		form_item_input.TComboBox( { field: 'status_id' } );
		form_item_input.setSourceData( $this.status_array );
		this.addEditFieldToColumn( $.i18n._( 'Status' ), form_item_input, tab_task_column1, '' );

		//		  Name
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'name', width: '100%' } );
		this.addEditFieldToColumn( $.i18n._( 'Name' ), form_item_input, tab_task_column1 );
		form_item_input.parent().width( '45%' );

		// Description
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'description', width: '100%' } );
		this.addEditFieldToColumn( $.i18n._( 'Description' ), form_item_input, tab_task_column1 );

		form_item_input.parent().width( '45%' );

		//Code
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'manual_id', width: 65 } );
		this.addEditFieldToColumn( $.i18n._( 'Code' ), form_item_input, tab_task_column1 );

		//Group
		form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );

		form_item_input.AComboBox( {
			tree_mode: true,
			allow_multiple_selection: false,
			layout_name: 'global_tree_column',
			set_empty: true,
			field: 'group_id'
		} );
		form_item_input.setSourceData( $this.job_item_group_array );
		this.addEditFieldToColumn( $.i18n._( 'Group' ), form_item_input, tab_task_column1 );

		// Product
		form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );

		form_item_input.AComboBox( {
			api_class: TTAPI.APIProduct,
			allow_multiple_selection: false,
			layout_name: 'global_product',
			show_search_inputs: true,
			set_empty: true,
			field: 'product_id'
		} );
		this.addEditFieldToColumn( $.i18n._( 'Product' ), form_item_input, tab_task_column1 );

		//Allowed GEO Fences
		if ( Global.getProductEdition() >= 20 ) {
			form_item_input = Global.loadWidgetByName( FormItemType.AWESOME_BOX );
			form_item_input.AComboBox( {
				api_class: TTAPI.APIGEOFence,
				allow_multiple_selection: true,
				layout_name: 'global_geo_fence',
				show_search_inputs: true,
				set_empty: true,
				field: 'geo_fence_ids'
			} );
			this.addEditFieldToColumn( $.i18n._( 'Allowed GEO Fences' ), form_item_input, tab_task_column1 );
		}

		//Tags
		form_item_input = Global.loadWidgetByName( FormItemType.TAG_INPUT );

		form_item_input.TTagInput( { field: 'tag', object_type_id: 610 } );
		this.addEditFieldToColumn( $.i18n._( 'Tags' ), form_item_input, tab_task_column1, '', null, null, true );

		var tab_task_column2 = tab_task.find( '.second-column' );

		this.edit_view_tabs[0].push( tab_task_column2 );

		//Estimated Time
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'estimate_time', mode: 'time_unit', need_parser_sec: true } );

		this.addEditFieldToColumn( $.i18n._( 'Estimated Time' ), form_item_input, tab_task_column2, '', null, true, true );

		//Estimated Quantity
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'estimate_quantity', width: 150 } );

		this.addEditFieldToColumn( $.i18n._( 'Estimated Quantity' ), form_item_input, tab_task_column2 );

		//Estimated Bad Quantity
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'estimate_bad_quantity', width: 150 } );

		this.addEditFieldToColumn( $.i18n._( 'Estimated Bad Quantity' ), form_item_input, tab_task_column2 );

		//Bad Quantity Billable Rate
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'bad_quantity_rate', width: 150 } );
		this.addEditFieldToColumn( $.i18n._( 'Bad Quantity Billable Rate' ), form_item_input, tab_task_column2 );

		//Billable Rate
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );

		form_item_input.TTextInput( { field: 'billable_rate', width: 86 } );
		this.addEditFieldToColumn( $.i18n._( 'Billable Rate' ), form_item_input, tab_task_column2 );

		//Minimum Billable Time
		form_item_input = Global.loadWidgetByName( FormItemType.TEXT_INPUT );
		form_item_input.TTextInput( { field: 'minimum_time', mode: 'time_unit', need_parser_sec: true } );

		this.addEditFieldToColumn( $.i18n._( 'Minimum Billable Time' ), form_item_input, tab_task_column2, '', null );
	}

	buildSearchFields() {

		super.buildSearchFields();
		this.search_fields = [

			new SearchField( {
				label: $.i18n._( 'Status' ),
				in_column: 1,
				field: 'status_id',
				multiple: true,
				basic_search: true,
				adv_search: true,
				layout_name: 'global_option_column',
				form_item_type: FormItemType.AWESOME_BOX
			} ),

			new SearchField( {
				label: $.i18n._( 'Code' ),
				in_column: 1,
				field: 'manual_id',
				multiple: true,
				basic_search: true,
				adv_search: true,
				form_item_type: FormItemType.TEXT_INPUT
			} ),

			new SearchField( {
				label: $.i18n._( 'Name' ),
				in_column: 2,
				field: 'name',
				multiple: true,
				basic_search: true,
				adv_search: true,
				form_item_type: FormItemType.TEXT_INPUT
			} ),

			new SearchField( {
				label: $.i18n._( 'Description' ),
				in_column: 1,
				field: 'description',
				multiple: true,
				basic_search: false,
				adv_search: true,
				form_item_type: FormItemType.TEXT_INPUT
			} ),

			new SearchField( {
				label: $.i18n._( 'Tags' ),
				field: 'tag',
				basic_search: true,
				adv_search: true,
				in_column: 1,
				object_type_id: 610,
				form_item_type: FormItemType.TAG_INPUT
			} ),

			new SearchField( {
				label: $.i18n._( 'Group' ),
				in_column: 2,
				multiple: true,
				field: 'group_id',
				layout_name: 'global_tree_column',
				tree_mode: true,
				basic_search: true,
				adv_search: true,
				form_item_type: FormItemType.AWESOME_BOX
			} ),

			new SearchField( {
				label: $.i18n._( 'Product' ),
				in_column: 2,
				field: 'product_id',
				layout_name: 'global_product',
				api_class: TTAPI.APIProduct,
				multiple: true,
				basic_search: false,
				adv_search: true,
				form_item_type: FormItemType.AWESOME_BOX
			} ),

			new SearchField( {
				label: $.i18n._( 'Created By' ),
				in_column: 2,
				field: 'created_by',
				layout_name: 'global_user',
				api_class: TTAPI.APIUser,
				multiple: true,
				basic_search: false,
				adv_search: true,
				form_item_type: FormItemType.AWESOME_BOX
			} ),

			new SearchField( {
				label: $.i18n._( 'Updated By' ),
				in_column: 2,
				field: 'updated_by',
				layout_name: 'global_user',
				api_class: TTAPI.APIUser,
				multiple: true,
				basic_search: false,
				adv_search: true,
				form_item_type: FormItemType.AWESOME_BOX
			} )
		];
	}

	setCurrentEditRecordData() {
		for ( var key in this.current_edit_record ) {
			var widget = this.edit_view_ui_dic[key];
			if ( Global.isSet( widget ) ) {
				switch ( key ) {
					default:
						widget.setValue( this.current_edit_record[key] );
						break;
				}
			}
		}

		this.collectUIDataToCurrentEditRecord();
		this.setEditViewDataDone();
	}

	onFormItemChange( target, doNotValidate ) {
		this.setIsChanged( target );
		this.setMassEditingFieldsWhenFormChange( target );
		var key = target.getField();
		var c_value = target.getValue();

//		switch ( key ) {
//			case 'estimate_time':
//			case 'minimum_time':
//				c_value = this.date_api.parseTimeUnit( target.getValue(), {async: false} ).getResult();
//				break;
//		}

		this.current_edit_record[key] = c_value;

		if ( !doNotValidate ) {
			this.validate();
		}

	}

}
